"use client";
import { Popup } from "react-map-gl/maplibre";

export default function ReportPopup({
  selectedReport,
  setSelectedReport,
  session,
  handleDelete,
}) {
  if (!selectedReport) return null;

  return (
    <Popup
      longitude={selectedReport.long}
      latitude={selectedReport.lat}
      closeButton
      closeOnClick={false}
      onClose={() => setSelectedReport(null)}
      anchor="top"
    >
      <div className="p-3 bg-white rounded shadow">
        <h3 className="font-bold">Food Truck Report</h3>
        <p className="text-sm">
          {selectedReport.description || "No description provided"}
        </p>
        <p>Submitted: {selectedReport.createdAt}</p>

        {session && session?.user?.id !== selectedReport.submittedById && (
          <div className="flex justify-between mt-2">
            <button className="bg-green-500 text-white px-3 py-1 rounded">
              ‚úÖ I See It
            </button>
            <button className="bg-red-500 text-white px-3 py-1 rounded">
              ‚ùå I Don't See It
            </button>
          </div>
        )}

        {session?.user?.id === selectedReport.submittedById && (
          <button
            className="bg-red-500 text-white px-3 py-1 mt-2 rounded hover:bg-red-700 transition"
            onClick={() => handleDelete(selectedReport.id)}
          >
            üóëÔ∏è Delete Report
          </button>
        )}
      </div>
    </Popup>
  );
}
